.schedule {
    background-image: url(../../images/background-image.jpg);
    display: flex;
    flex-direction: column;
    padding-bottom: 50px;
}

.schedule__width {
    margin: 0 auto;
    z-index: 50;
}

.schedule__title {
    font-family: "Cormorant Garamond", serif;
    margin: 0;
    padding: 0;
    font-size: 59px;
    font-weight: 200;
    z-index: 50;
    opacity: 0;
    transform: translateY(20px);
    transition: opacity 4s ease-out, transform 4s ease-out;
}

.schedule__card-flex {
    margin-top: 25px;
    display: flex;
    justify-content: flex-start;
    opacity: 0;
    transform: translateY(20px);
    transition: opacity 4s ease-out, transform 4s ease-out;
}

.schedule__cart {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    z-index: 50;
}

.schedule__paragraph {
    margin: 0;
    padding: 0;
    font-family: "Cormorant Garamond", serif;
    font-weight: 300;
    font-size: 40px;
    z-index: 50;
    margin-bottom: 10px;
}

.schedule__paragraph-down {
    margin: 0;
    padding: 0;
    font-family: "Cormorant Garamond", serif;
    font-weight: 300;
    font-size: 25px;
    color: rgb(78, 78, 78);
    z-index: 50;
}

.schedule__time {
    margin: 0;
    padding: 0;
    margin-right: 105px;
    font-family: "Cormorant Garamond", serif;
    font-weight: 300;
    font-size: 30px;
    z-index: 50;
}

.schedule__map {
    margin-top: 25px;
    margin-bottom: 25px;
    overflow: hidden;
    padding-bottom: 56.25%;
    position: relative;
    height: 0;
}

.schedule__map iframe {
    border: 0;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    position: absolute;
}

.schedule__title.visible,
.schedule__card-flex.visible {
    opacity: 1;
    transform: translateY(0);
}

@media (max-width: 720px) {
    .schedule__title {
        font-size: 45px;
    }
}

@media (max-width: 580px) {
    .schedule__time {
        margin-right: 20px;
    }
}

@media (max-width: 450px) {
    .schedule__title {
        font-size: 32px;
    }

    .schedule__paragraph {
        font-size: 25px;
    }

    .schedule__paragraph-down {
        font-size: 15px;
    }
}
